.welcome-main{display:flex;justify-content:space-around;font-size:84px;font-family:"MomcakeBold";width:100%}.welcome-main .left-main{flex:66% 1;min-height:100vh;display:flex;justify-content:flex-end;align-items:flex-end;margin-right:100px}.welcome-main .left-main div .name{position:absolute;margin:0;padding-top:150px;padding-left:10px}.welcome-main .left-main div .headshot{height:600px;width:550px;src:url(/static/media/headshot_nobackground.e7b35c3a.png);animation:headshot 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}.right-main{margin-right:50px;align-self:center;width:75%;z-index:1}.right-main .title{font-size:20px;color:#C86142;text-decoration:underline}.right-main .welcome-contact{color:#C86142;font-size:18px}.side-section{display:flex;flex-direction:column}.shapes{height:300px;width:300px;margin:50px 0 -100px 0}.email-link,.email-link:hover,.email-link:focus,.email-link:after{text-decoration:none;color:#C86142}.location,.intro{margin:0;font-size:52px}.entrance{animation:entrance 2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}.exit{animation:exit 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) 1s both}.name{animation:name 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}@keyframes name{0%{transform:translateX(-1000px);opacity:0}100%{transform:translateX(0);opacity:1}}@keyframes entrance{0%{transform:scale(0);opacity:1}100%{transform:scale(1);opacity:1}}@keyframes exit{0%{transform:scale(1);opacity:1}100%{transform:scale(0);opacity:1}}@keyframes headshot{0%{transform:scale(0);opacity:1}100%{transform:scale(1);opacity:1}}.location{animation:location 2.5s cubic-bezier(0.39, 0.575, 0.565, 1) both}@keyframes location{0%{opacity:0}100%{opacity:1}}.intro{animation:intro 2s cubic-bezier(0.39, 0.575, 0.565, 1) 2s both}@keyframes intro{0%{opacity:0}100%{opacity:1}}.intro-span{animation:intro-span 2s cubic-bezier(0.39, 0.575, 0.565, 1) 3s both}@keyframes intro-span{0%{opacity:0}100%{opacity:1}}.load-last{animation:load-last 2s cubic-bezier(0.39, 0.575, 0.565, 1) 4s both}@keyframes load-last{0%{opacity:0}100%{opacity:1}}

